"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { Menu, X, BarChart2, Home, Activity, FileText, Settings, Sun, Moon, Laptop, TrendingUp, Newspaper } from "lucide-react";
import { useTheme } from "next-themes";
import { cn } from "@/lib/utils";
import { useAuth } from "@/context/AuthContext";

// Navigation Links
const navLinks = [
    { name: "Home", href: "/", icon: Home },
    { name: "Dashboard", href: "/dashboard", icon: BarChart2 },
    { name: "Strength", href: "/strength-analyzer", icon: Activity },
    { name: "God Mode", href: "/god-mode", icon: TrendingUp },
    { name: "News", href: "/news", icon: Newspaper }, // Changed Blogs to News based on context or add new? User asked for news page. I'll add News alongside Blogs.
    { name: "Blogs", href: "/blog", icon: FileText },
    { name: "Admin", href: "/admin", icon: Settings },
];

export default function GlobalNav() {
    const pathname = usePathname();
    const { theme, setTheme } = useTheme();
    const [isScrolled, setIsScrolled] = useState(false);
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
    const [mounted, setMounted] = useState(false);

    // Handle scroll effect
    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 20);
        };
        window.addEventListener("scroll", handleScroll);
        setMounted(true);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    if (!mounted) return null;

    return (
        <>
            <nav
                className={cn(
                    "fixed top-0 left-0 right-0 z-50 transition-all duration-300",
                    isScrolled
                        ? "bg-background/70 backdrop-blur-xl border-b border-white/10 shadow-sm py-3"
                        : "bg-transparent py-5"
                )}
            >
                <div className="container mx-auto px-6 flex items-center justify-between">
                    {/* Logo */}
                    <Link href="/" className="flex items-center gap-2 group">
                        <div className="bg-primary/10 p-2 rounded-xl group-hover:bg-primary/20 transition-colors">
                            <BarChart2 className="w-6 h-6 text-primary" />
                        </div>
                        <span className="text-xl font-bold tracking-tight text-foreground">
                            NG <span className="text-primary">Trade Analysis</span>
                        </span>
                    </Link>

                    {/* Desktop Nav */}
                    <div className="hidden md:flex items-center gap-1">
                        {navLinks.map((link) => {
                            const isActive = pathname === link.href;
                            const Icon = link.icon;
                            return (
                                <Link
                                    key={link.href}
                                    href={link.href}
                                    className={cn(
                                        "relative px-4 py-2 rounded-full text-sm font-medium transition-colors flex items-center gap-2",
                                        isActive
                                            ? "text-primary-foreground"
                                            : "text-muted-foreground hover:text-foreground hover:bg-accent/50"
                                    )}
                                >
                                    {isActive && (
                                        <motion.div
                                            layoutId="activeNav"
                                            className="absolute inset-0 bg-primary rounded-full shadow-lg shadow-primary/25"
                                            transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                                        />
                                    )}
                                    <span className="relative z-10 flex items-center gap-2">
                                        <Icon className="w-4 h-4" />
                                        {link.name}
                                    </span>
                                </Link>
                            );
                        })}
                    </div>

                    {/* Right Actions */}
                    <div className="hidden md:flex items-center gap-4">
                        {/* Theme Toggle */}
                        <div className="flex items-center bg-accent/20 rounded-full p-1 border border-white/5">
                            <button
                                onClick={() => setTheme("light")}
                                className={cn("p-1.5 rounded-full transition-all", theme === "light" && "bg-background shadow-sm text-yellow-500")}
                            >
                                <Sun className="w-4 h-4" />
                            </button>
                            <button
                                onClick={() => setTheme("system")}
                                className={cn("p-1.5 rounded-full transition-all", theme === "system" && "bg-background shadow-sm text-primary")}
                            >
                                <Laptop className="w-4 h-4" />
                            </button>
                            <button
                                onClick={() => setTheme("dark")}
                                className={cn("p-1.5 rounded-full transition-all", theme === "dark" && "bg-background shadow-sm text-indigo-400")}
                            >
                                <Moon className="w-4 h-4" />
                            </button>
                        </div>

                        {/* Auth Buttons */}
                        {user ? (
                            <div className="flex items-center gap-3">
                                <div className="text-sm font-medium hidden lg:block">
                                    {user.first_name}
                                </div>
                                <button
                                    onClick={logout}
                                    className="px-4 py-2 bg-red-500/10 hover:bg-red-500/20 text-red-500 text-sm font-semibold rounded-full transition-colors border border-red-500/20"
                                >
                                    Logout
                                </button>
                            </div>
                        ) : (
                            <div className="flex items-center gap-3">
                                <Link
                                    href="/signin"
                                    className="px-5 py-2 text-sm font-semibold text-muted-foreground hover:text-foreground transition-colors"
                                >
                                    Log In
                                </Link>
                                <Link
                                    href="/signup"
                                    className="px-5 py-2 bg-primary hover:bg-primary/90 text-primary-foreground text-sm font-bold rounded-full transition-all shadow-lg shadow-primary/25"
                                >
                                    Sign Up
                                </Link>
                            </div>
                        )}
                    </div>

                    {/* Mobile Toggle */}
                    <button
                        className="md:hidden p-2 text-muted-foreground hover:text-foreground"
                        onClick={() => setMobileMenuOpen(true)}
                    >
                        <Menu className="w-6 h-6" />
                    </button>
                </div>
            </nav>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {mobileMenuOpen && (
                    <>
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 md:hidden"
                            onClick={() => setMobileMenuOpen(false)}
                        />
                        <motion.div
                            initial={{ x: "100%" }}
                            animate={{ x: 0 }}
                            exit={{ x: "100%" }}
                            transition={{ type: "spring", damping: 25, stiffness: 200 }}
                            className="fixed top-0 right-0 bottom-0 w-[280px] bg-background border-l border-border z-50 p-6 md:hidden shadow-2xl"
                        >
                            <div className="flex items-center justify-end mb-8">
                                <button
                                    onClick={() => setMobileMenuOpen(false)}
                                    className="p-2 hover:bg-accent rounded-full"
                                >
                                    <X className="w-6 h-6" />
                                </button>
                            </div>

                            <div className="flex flex-col gap-2">
                                {navLinks.map((link) => {
                                    const Icon = link.icon;
                                    return (
                                        <Link
                                            key={link.href}
                                            href={link.href}
                                            onClick={() => setMobileMenuOpen(false)}
                                            className={cn(
                                                "flex items-center gap-4 px-4 py-3 rounded-xl text-lg font-medium transition-colors",
                                                pathname === link.href
                                                    ? "bg-primary/10 text-primary"
                                                    : "text-muted-foreground hover:bg-accent hover:text-foreground"
                                            )}
                                        >
                                            <Icon className="w-5 h-5" />
                                            {link.name}
                                        </Link>
                                    );
                                })}
                            </div>

                            <div className="absolute bottom-8 left-6 right-6">
                                <div className="flex justify-between items-center bg-accent/30 p-2 rounded-2xl">
                                    <button onClick={() => setTheme("light")} className={cn("flex-1 p-2 rounded-xl flex justify-center", theme === "light" && "bg-background shadow-sm")}>
                                        <Sun className="w-5 h-5 text-yellow-500" />
                                    </button>
                                    <button onClick={() => setTheme("system")} className={cn("flex-1 p-2 rounded-xl flex justify-center", theme === "system" && "bg-background shadow-sm")}>
                                        <Laptop className="w-5 h-5 text-primary" />
                                    </button>
                                    <button onClick={() => setTheme("dark")} className={cn("flex-1 p-2 rounded-xl flex justify-center", theme === "dark" && "bg-background shadow-sm")}>
                                        <Moon className="w-5 h-5 text-indigo-400" />
                                    </button>
                                </div>
                            </div>
                        </motion.div>
                    </>
                )}
            </AnimatePresence>
        </>
    );
}
